html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
}
.mdl-typography--text-large {
  font-size: 16px;
}
.content .mdl-grid {
  padding: 10px 40px;
}
i {
  vertical-align: middle;
}
.header-contact {
  margin-top: 28px;
}
.header-contact--inner {
  width: 100%;
  display: flex;
}
.header-contact a {
  flex: 1;
}
.header-contact .fa {
  font-size: 40px;
  color: #424242 !important;
}
.content .in-progress {
  list-style-type: circle;
}
.content ul {
  margin: 0;
}
.ul-horizontal li {
  float: Left;
}


footer .fa {
  width: 1.28571429em;
  text-align: center;
  margin-right: 20px;
  font-size: 18px;
}
footer li {
  padding: 5px;
}
